import React, { Component } from "react";
import { Switch, Route } from "react-router-dom";
import projectFirestore from "../../Config/config";
import "./Shop.scss";
import Checkout from "../Checkout/Checkout";
class Shop extends Component {
  constructor() {
    super();
    this.state = {
      products: [],
      productListed: false,
      purchaseList: [],
    };
  }
  products = [];

  //TO FETCH THE PRODUCT DATA
  componentDidMount() {
    projectFirestore
      .collection("products")
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          this.products.push({
            name: doc.data().name,
            price: doc.data().price,
          });
        });
        const newState = {
          ...this.state,
        };
        newState.products = this.products;
        newState.productListed = true;
        this.setState(newState);
      });
  }
  //PURCHASE CHICK EVENT HANDLER
  purchaseHandler = (productName,productPrice) => {
    const newState = {
      ...this.state,
    };
    //ADD THE PRODUCT IF IT DOES NOT EXIST
    let found = false;
    for (let i = 0; i < this.state.purchaseList.length; i++) {
      if (this.state.purchaseList[i].productName === productName) {
        found = true;
        break;
      }
    }
    if (!found) {
      const value = 1;
      newState.purchaseList.push({
        productName: productName,
        quantity: value,
        quantityNull: false,
        productPrice: productPrice
      });
    }
    //REMOVE THE PRODUCT IF IT DOES EXIST
    else {
      for (var i = newState.purchaseList.length - 1; i >= 0; i--) {
        if (newState.purchaseList[i].productName === productName) {
          newState.purchaseList.splice(i, 1);
        }
      }
    }
    this.setState(newState);
  };

  render() {
    let productDisplay = null;
    //PRODUCT LISTED ON SIGHT
    if (this.state.productListed) {
      productDisplay = this.state.products.map((item) => {
        let returnValue = <p> Add To Cart </p>;
        let found = false;
        for (let i = 0; i < this.state.purchaseList.length; i++) {
          if (this.state.purchaseList[i].productName === item.name) {
            found = true;
            break;
          }
        }
        if (found) {
          returnValue = <p className="Active"> Added To Cart </p>;
        }
        return (
          <div className="product-items" key={item.name}>
            <div className="items-value">
              <h3> {item.name.toUpperCase()} </h3> <p> Rs: {item.price} </p>{" "}
            </div>
            <div
              className="buy-button"
              onClick={() => this.purchaseHandler(item.name, item.price)}
            >
              {returnValue}{" "}
            </div>{" "}
          </div>
        );
      });
    }

    return (
      <div className="shop">
      <Switch>
      {/*ROUTE 1*/}
          <Route exact path="/shop">
          <h2> SHOP </h2>
          <div className="products"> {productDisplay} </div>
          </Route>
     {/*ROUTE TO CART*/}
          <Route exact path="/shop/checkout">
            <Checkout cart={this.state.purchaseList} />
          </Route>
        </Switch>
      </div>
    );
  }
}

export default Shop;
