import React from 'react';
import Navitems from './Navitems/Navitems';
import './Navbar.scss';
import navLogo from '../../assets/svg/Bishnu.png';
import { NavLink } from 'react-router-dom';
import './active.scss';
const Navbar = (prop) => {
  return (
    <React.Fragment>
      <div className="navContainer">
        <img src={navLogo} alt=''></img>
        <nav>
          <NavLink exact to='/'>
            <Navitems name='Home' />
          </NavLink>

          <NavLink exact to='/shop'>
            <Navitems name='Shop' />
          </NavLink>
          <NavLink exact to='/shop/checkout'>
            <Navitems name='Checkout' />
          </NavLink>
          <NavLink exact to='/about'>
            <Navitems name='About' />
          </NavLink>
        </nav>
      </div>
    </React.Fragment>
  );
};
export default Navbar;
