import React from 'react';
import Button from '../../components/UI/Buttons/Button';
import classes from './Home.module.scss';
import Title from '../../components/UI/Title/Title-bg';
import { Link } from 'react-router-dom';
const home = () => {
  return (
    <React.Fragment>
      <section className={classes.main}>
        <div>
          <Title>Bishnu Electricals</Title>
          <p className={classes.p}>
            हामी कहाँ बिजुली र वायरिङ्ग सम्बन्धि सम्पूर्ण सामग्री तथा सेवा सुपथ
            मुल्यमा उपलब्ध छ।
          </p>
        </div>
        <div className={classes.buttons}>
          <Link to='/about'>
            <Button type='orange'>Contact Us</Button>
          </Link>
          <Link to='/shop'>
            <Button type='blue'>Shop Now</Button>
          </Link>
        </div>
      </section>
    </React.Fragment>
  );
};

export default home;
