import React from 'react';
import './CartItems.scss';
 const cartItems = (props) => {
    return <React.Fragment>
            <div className="cartItem">
                  <h3 className='name'>{props.name}</h3>
                        <div className="btns">
                          <div className="dec" onClick={props.dec}>{"<"}</div>
                          <p> {props.qnty} </p>
                          <div className="inc" onClick={props.inc}>{">"}</div>
                        </div>
                    <h3 className='price'>Rs:{props.total}</h3>
            </div>
    </React.Fragment>
}
export default cartItems;