import React, { Component } from 'react';
import './Checkout.scss';
import projectFirestore from '../../Config/config';
import Form from './../../components/UI/Form/Form';
import Title from '../../components/UI/Title/Title';
import CartItem from '../../components/UI/CartItems/CartItems';
class Checkout extends Component {
  constructor(props) {
    super(props);
    this.state = {
      cartList: props.cart,
      userDetail : null,
      userCache: {
        name:'',
        phone:'',
        email:'',
        address:'',
      },
      Total: null
    }

  }
  Total =null ;

  userDetailHandler = () =>{
    const newState = {...this.state};
    newState.userDetail = {...newState.userCache};
    newState.Total = this.Total; 
    this.setState(newState);
    if(this.state.Total)
    {
      this.fireStore(newState);
      alert("* Delivery Fee is Not Included. * \nYour Order Placed. Thank You For Visiting Us. ");  
    }
    
    else
    alert( `CART EMPTY`) ;
     
  }
  fireStore = (obj) => {
    projectFirestore.collection("users").add(obj)
  .then(function(docRef) {
      console.log("Document written with ID: ", docRef.id);
  })
  .catch(function(error) {
      console.error("Error adding document: ", error);
  });
  }

  changeHandler = (obj) => {
    const newState = {
      ...this.state
    }

    newState.userCache[obj.name] =obj.value;
    this.setState(newState);
  }


  incrementHandler = (e) => {
    const newState = {
      ...this.state
    }
    if(e.quantity < 12) {
      for (var i = this.state.cartList.length - 1; i >= 0; i--) {
        if (newState.cartList[i].productName === e.productName) {
          newState.cartList[i].quantity= parseInt(newState.cartList[i].quantity) + 1 ;
        }
      } 
    }
    this.setState(newState);
  }

  decrementHandler = (e) => {
    const newState = {
      ...this.state
    }
    if(e.quantity > 0) {
      for (var i = this.state.cartList.length - 1; i >= 0; i--) {
        if (newState.cartList[i].productName === e.productName) {
          newState.cartList[i].quantity= parseInt(newState.cartList[i].quantity) - 1 ;

        }
      } 
    }
    this.setState(newState);
  }
  render() {
    let Total = "CART EMPTY";
    let cartItems = null;
    if(this.state.cartList) {
      cartItems = this.state.cartList.map(item => {
        let total = parseInt(item.productPrice) * parseInt(item.quantity);
        if(Total === "CART EMPTY") 
        { Total = null;}

        Total= Total + total;
        this.Total =Total;

        return (<CartItem name={item.productName}
                          key={item.productName}
                          qnty= {item.quantity} 
                          total= {total}
                          inc =  {()=> this.incrementHandler(item)}
                          dec = {()=> this.decrementHandler(item)} />)
      });
    }
    return (
      <React.Fragment>
        <div className="checkout">
          <div className="checkoutList">
          <Title> Cart Summery</Title>
          <div className="cartItems">
          {cartItems}
          </div>
          <div className="total">
            <h4>Total Price: </h4>
            <h3> {Total}</h3>
          </div>
          </div>
          <div className="form" >
            <h2>Checkout Form</h2>
            <Form changeHandler={this.changeHandler}/>
            <p>Please fillup the Form with Detail address and number.</p>
            <div className="button" onClick={()=>this.userDetailHandler()}>
              <h3>Place Order</h3>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Checkout;
