import React, {
  useState
} from "react";
import Input from "../Input/input";
const Form = (props) => {
  const [formData] = useState([{
      id: "name",
      label: "Name",
      type: "Text",
      placeholder: "Your Name"
    },
    {
      id: "phone",
      label: "Phone No",
      type: "tel",
      placeholder: "Mobile or Landline Number"
    },
    {
      id: "email",
      label: "Email",
      type: "email",
      placeholder: "your@email_here.com"
    },
    {
      id: "address",
      label: "Address",
      type: "text",
      placeholder: "Your Full Address"
    },
  ]);

  const form = formData.map((input) => {
    return ( <
      Input key = {input.id}
      id = {input.id}
      label = {input.label}
      type = {input.type}
      placeholder = {input.placeholder}
      changeHandler = {props.changeHandler}
      />
    );
  });
  return <React.Fragment > {form} </React.Fragment>;
};

export default Form;